:root {
  --color-accent: #5a004b;
  --color-soft: #888;
  --color-strong: #252525;
  --color-background: #000;
  --color-accent2: #00badb;
  --color-box-width: calc(20% - 8px);
  --color-box-height: 32px;
  --header-height: 64px;
  --caption-paint-groups: "paint by group";
  --caption-paint-hover: "paint with mouse move over";
  --caption-minimize-menu: "minimize menu";
  --caption-enlarge-menu: "unwrap menu";
  --caption-color-menu: "change colors";
  --caption-undo: "undo (Ctrl+Z)";
  --caption-revert: "revert (Ctrl+Shift+Z)";
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  background-color: var(--color-background);
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.image-container {
  z-index: 1;
  max-width: 1024px;
  margin: var(--header-height) auto 0;
}

@media screen and (max-width: 768px) {
  .image-container {
    margin-left: 42px;
  }
}

header {
  z-index: 100;
  width: 100%;
  height: var(--header-height);
  background-color: #000;
  border-bottom: 1px solid #fff;
  justify-content: center;
  align-items: center;
  padding-left: 24px;
  padding-right: 12px;
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.logo__header {
  color: #fff;
  font-family: Philosopher, sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.12;
}

.main-menu {
  height: 42px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 12px;
  margin-left: 42px;
  padding: 12px;
}

.main-menu__items {
  display: flex;
}

.main-menu__items > li ~ * {
  margin-left: 24px;
}

.main-menu__items > li > a {
  color: #fff;
  padding: 12px 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
  font-weight: 700;
}

.main-menu__items > li > a:hover, .main-menu__items > li > a:focus, .main-menu__items > li > a:active {
  color: var(--color-accent2);
}

.main-menu__open-btn {
  width: 54px;
  height: 54px;
  color: #fff;
  cursor: pointer;
  border: 1.5px solid #fff;
  border-radius: 50%;
  margin-left: auto;
  font-size: 18px;
  display: none;
}

.main-menu__open-btn:hover, .main-menu__open-btn:active {
  color: var(--color-accent2);
  border-color: var(--color-accent2);
}

.main-menu__open-btn > span {
  padding: 12px;
  font-size: 24px;
  display: block;
}

@media screen and (max-width: 768px) {
  .main-menu {
    z-index: 199;
    top: calc(var(--header-height)  + 12px);
    height: calc(100vh - var(--header-height)  - 24px);
    width: 250px;
    background-color: #000;
    border: 1.5px solid #fff;
    margin-left: 0;
    transition: all .125s linear;
    position: absolute;
    right: 12px;
    transform: translate(120%);
  }

  .main-menu.is-open {
    transform: none;
  }

  .main-menu__items {
    display: block;
  }

  .main-menu__items > li ~ * {
    margin-top: 8px;
    margin-left: 0;
  }

  .main-menu__items > li > a {
    color: #fff;
    padding: 8px 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 14px;
    display: block;
  }

  .main-menu__open-btn {
    display: block;
  }
}

.menu__container {
  z-index: 99;
  width: auto;
  height: auto;
  left: 0;
  top: var(--header-height);
  position: fixed;
}

.btn__icon {
  width: 100%;
  height: 100%;
  fill: var(--color-accent);
}

.is-active > .btn__icon {
  fill: #fff;
}

.menu-btn {
  width: 32px;
  height: 32px;
  border: 1.5px solid var(--color-accent);
  color: var(--color-accent);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  padding: 8px;
  font-family: Courier New, Courier, monospace;
  font-size: 12px;
  font-weight: 700;
  display: block;
  position: absolute;
}

.menu-btn.menu-btn__relative {
  margin-top: 8px;
  position: relative;
}

.menu-btn.is-active {
  border: 1.5px solid var(--color-accent);
  background-color: var(--color-accent);
  color: #fff;
}

.js-minimize-btn {
  top: 12px;
  right: 14px;
}

.js-color-list-btn {
  top: 12px;
  right: 54px;
}

.js-current-color {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  padding: 8px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(25%, 25%);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .5);
}

.color-menu {
  z-index: 102;
  width: 25vw;
  min-width: 250px;
  max-width: 350px;
  height: auto;
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  padding: 24px;
  transition: all .125s linear;
  position: relative;
  top: 12px;
  left: 12px;
}

.color-menu__header {
  text-align: left;
  color: var(--color-accent);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}

.color-menu__header ~ * {
  margin-top: 16px;
}

.pallette-name {
  text-align: center;
  color: var(--color-soft);
  margin-top: 12px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
}

.color-menu > ul {
  flex-wrap: true;
  background-color: #fff;
  flex-direction: row;
  margin-left: -8px;
  padding: 0;
  list-style: none;
  display: flex;
}

.color-menu > ul > li {
  width: var(--color-box-width);
  height: 24px;
  cursor: pointer;
  border-radius: 4px;
  margin-left: 8px;
  padding: 0;
  display: block;
}

.color-menu.minimized {
  width: 42px;
  min-width: 0;
  height: auto;
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  padding: 56px 4px 24px;
}

.color-menu.minimized > .js-color-list-btn, .color-menu.minimized > h2, .color-menu.minimized > p {
  display: none;
}

.color-menu.minimized > .js-minimize-btn {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  padding: 8px;
  font-size: 12px;
  right: 5px;
}

.color-menu.minimized > .js-current-color {
  width: 32px;
  height: 32px;
  border-radius: 4px;
  transform: translate(25%, 50%);
}

.color-menu.minimized > ul {
  flex-wrap: false;
  flex-direction: column;
  margin-top: -5px;
  margin-left: 0;
}

.color-menu.minimized > ul > li {
  width: auto;
  height: var(--color-box-height);
  border-radius: 4px;
  margin-top: 5px;
  margin-left: 0;
}

@media screen and (min-width: 769px) {
  .menu-btn:hover:before {
    display: block;
  }
}

.menu-btn:before {
  z-index: 199;
  color: #000;
  background-color: #fff;
  border-radius: 8px;
  padding: 8px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
  font-weight: 300;
  display: none;
  position: absolute;
  top: 50%;
  left: -8px;
  transform: translate(-100%, -50%);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .5);
}

.js-group-tool-btn:before {
  content: var(--caption-paint-groups);
}

.js-hover-tool-btn:before {
  content: var(--caption-paint-hover);
}

.js-minimize-btn:before {
  content: var(--caption-minimize-menu);
}

.js-undo-tool-btn:before {
  content: var(--caption-undo);
}

.js-revert-tool-btn:before {
  content: var(--caption-revert);
}

.minimized > .menu-btn:before {
  left: 42px;
  transform: translate(0, -50%);
}

.minimized > .js-minimize-btn:before {
  content: var(--caption-enlarge-menu);
}

.js-color-list-btn:before {
  content: var(--caption-color-menu);
}

.tools-menu {
  z-index: 101;
  width: 42px;
  height: auto;
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  margin-top: 24px;
  padding: 8px 5px;
  transition: all .125s linear;
  position: relative;
  top: 12px;
  left: 12px;
}

.menu-container {
  width: 300px;
  text-align: center;
  background-color: #fff;
  border-radius: 12px;
  padding: 50px 25px;
}

.js-pallette-list {
  max-height: 65vh;
  overflow: auto;
}

.pallette-menu__header {
  text-align: center;
  color: var(--color-accent);
  margin-bottom: 16px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  display: block;
}

.js-warning-alert {
  font-size: 14px;
  font-weight: 400;
}

.js-warning-alert__yes, .js-warning-alert__no {
  text-transform: uppercase;
  cursor: pointer;
  margin: 25px 25px 0;
  font-size: 18px;
  font-weight: 700;
  display: inline-block;
}

.js-warning-alert__yes:hover, .js-warning-alert__no:hover {
  color: var(--color-accent);
}

.link {
  color: var(--color-strong);
  cursor: pointer;
  font-weight: 400;
}

.link:hover, .link:active, .link:focus {
  color: var(--color-accent);
  font-weight: 700;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: visible;
  opacity: 0;
  pointer-events: none;
  transform: scale(150%);
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .125s linear, transform .125s ease-in-out, -webkit-transform .125s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 101;
  max-width: 94%;
  transition: opacity .125s linear, transform .125s ease-in-out, -webkit-transform .125s ease-in-out;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

